@import url(https://fonts.googleapis.com/css2?family=Grenze+Gotisch:wght@400;600&family=Roboto:ital,wght@0,400;0,700;1,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Germania+One&family=Pirata+One&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.Header{position:fixed;width:100%;min-height:4rem;display:flex;justify-content:space-around}.Header .orderbutton{margin:auto 1rem auto auto}

.Footer{flex-shrink:0}

.Jumbotron{display:grid;grid-template-columns:33.33% 33.33% 33.33%;grid-template-rows:25% 25% 10% 10% 10% 10% 10%;width:100%;height:100vh;min-height:-webkit-fit-content;min-height:-moz-fit-content;min-height:fit-content}.Jumbotron .jumbo{font-size:1.75rem;font-weight:bold;color:white;margin:auto}.Jumbotron .jumbo-background{position:absolute;z-index:-1;height:100%;width:100%;overflow:hidden}.Jumbotron .jumbo-background img{min-height:100%;min-width:100%}.Jumbotron .jumbo-name{grid-column-start:span 2;grid-row-start:span 2;font-size:10rem;line-height:9rem;color:white;width:33vw;padding:24px}.Jumbotron .jumbo-location{grid-row-start:3}.Jumbotron .jumbo-hours{grid-row-start:6}.Jumbotron .jumbo-contact{grid-column-start:3;grid-row-start:6}

.Home{display:flex;min-height:-webkit-fit-content;min-height:-moz-fit-content;min-height:fit-content;flex-direction:column}.Home .story-card{background-image:url(/deerhead/static/media/sh.griffin.lg.78339b57.jpg);background-size:107%;background-position:center center}.Home .menu-card{background-image:url(/deerhead/static/media/sh.griffin.lg.78339b57.jpg);background-size:107%;background-position:center center}.Home .order-card{background-image:url(/deerhead/static/media/sh.griffin.lg.78339b57.jpg);background-size:107%;background-position:center center}


html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline}article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block}body{line-height:1}ol,ul{list-style:none}blockquote,q{quotes:none}blockquote:before,blockquote:after,q:before,q:after{content:'';content:none}table{border-collapse:collapse;border-spacing:0}body{background-color:#C8A84F}body h1{font-family:"Pirata One",cursive;font-size:3rem}body p{font-family:"Roboto",sans-serif;font-size:1.2rem}body button{padding:12px 24px;margin:auto 0;border-radius:12px;font-size:1.2rem}body button:hover{background-color:#dddddd}.scrim{position:absolute;height:100%;width:100%;background-color:rgba(0,0,0,0.65)}.popup{position:absolute;height:100%;width:100%;z-index:10}.spacer{clear:both}.content-row{display:flex;justify-content:space-around;min-height:-webkit-fit-content;min-height:-moz-fit-content;min-height:fit-content;background:#333333;color:white;margin:1rem;border:5px solid red;border-radius:25px}.content-row .content-card{display:flex;min-height:33vh;min-width:18vw;align-items:center;justify-content:center;margin:24px;padding:25px;border:5px solid white;border-radius:25px}.App{display:flex;flex-direction:column;min-height:100vh}.App .About,.App .Contact,.App .Home{flex:1 0 auto}

